<template>
    <div class="fixed-header">
        <div class="navbar">
            <div class="logo">
                <svg-icon icon-class="logo" class-name="logo-img"></svg-icon>
            </div>
            <!-- <div class="search-box">
                <i class="el-icon-search" style="color:#909CB1"></i>
                <input type="text" class="gray-color" placeholder="Search for...">
            </div> -->
            <span ref="pointSpan" class="point-text">{{nowPointLabel}}</span>
            <div class="right-menu" v-if="nowRole=='jd'">
                <div class="right-menu-item crew">{{team}}</div>
                <i class="right-menu-item el-icon-message-solid" style="color:#909CB1"></i>
                <div class="head-img"></div>
                <div class="select-names" :style="'width:' + pointBoxWidth+'px'">
                    <el-select v-model="nowPoint" @change="handPoint">
                        <el-option v-for="item in options" :key="item.value" :label="item.label" :value="item.value">
                        </el-option>
                    </el-select>
                </div>
                <el-dropdown trigger="click" @command="handleCommand">
                    <span class="el-dropdown-link">
                        <i class="el-icon-s-tools"></i>
                    </span>
                    <el-dropdown-menu slot="dropdown">
                        <el-dropdown-item command="changePassword" class="iconfont cl-icon-logonout logout">
                            <span>修改密码</span>
                        </el-dropdown-item>
                        <el-dropdown-item command="logout" class="iconfont cl-icon-logonout logout"><span>登出</span>
                        </el-dropdown-item>

                    </el-dropdown-menu>
                </el-dropdown>
            </div>
            <div class="right-menu" v-else>
                <div class="head-img"></div>
                <div class="right-menu-item crew margin-right">
                    {{nickName}}
                </div>
                <el-dropdown trigger="click" @command="handleCommand">
                    <span class="el-dropdown-link">
                        <i class="el-icon-s-tools"></i>
                    </span>
                    <el-dropdown-menu slot="dropdown">
                        <el-dropdown-item command="changePassword" class="iconfont cl-icon-logonout logout">
                            <span>修改密码</span>
                        </el-dropdown-item>
                        <el-dropdown-item command="logout" class="iconfont cl-icon-logonout logout"><span>登出</span>
                        </el-dropdown-item>

                    </el-dropdown-menu>
                </el-dropdown>
            </div>

        </div>
        <ModelPassword :show="this.showModel" :loading="loading" @submit="submit"></ModelPassword>
    </div>
</template>
<script>
    import {
        mapGetters,
        mapState,
        mapActions
    } from 'vuex'
    import {
        getPoints,
        changePoint
    } from '@/api/home'
    import ModelPassword from "./ModelPassword.vue"; //异常处理
    import {
        ROLENAME
    } from "@/utils/config.js";
    import store from '@/store'
    export default {
        name: 'slierHeader',
        components: {
            ModelPassword,
        },

        data() {
            return {
                nowPoint: '',
                nowPointLabel: '', //选中的名称
                options: [],
                pointBoxWidth: 0, // 进队名称div的宽度
                nowRole: '',
                showModel: false, //修改密码框弹窗内容
                loading: false, //加载状态
            }
        },
        computed: {
            ...mapState({
                userId: state => state.user.userId,
                team: state => state.user.team,
                points: state => state.user.points,
                roles: state => state.user.roles,
                nickName: state => state.user.nickName,
            }),
        },
        watch: {
            points: {
                deep: true,
                handler(val) {
                    this.initOptions()
                }
            }
        },
        created() {
            this.nowRole = this.roles;
            if (this.roles && this.roles.length > 0 && (this.roles.includes(ROLENAME.finance) || this.roles.includes(
                    ROLENAME
                    .yw) || this.roles.includes(ROLENAME
                    .marketer)))
                this.nowRole = 'yw'
            else this.nowRole = 'jd'

            this.initOptions()
        },
        mounted() {
            store.dispatch('GetPoints').then(res => {}) //获取当前用户下的计量点
            store.dispatch('GetDetail').then(res => { //获取用户信息
            })
            this.$nextTick(() => {
                this.setWith()
            })
        },
        updated() {
            this.$nextTick(() => {
                this.setWith()
            })
        },
        methods: {
            ...mapActions([
                'LogOut',
                'ResetPwd'
            ]),
            setWith() { //计算井队名称的长度 动态设置
                const width = this.$refs['pointSpan'].clientWidth;
                this.pointBoxWidth = width + 50;
            },
            initOptions() {
                // console.log("ccccccc", this.points)
                this.options.length = 0; //先清空
                this.points ? this.points.map(item => {
                    if (item.alive == 1) {
                        this.nowPoint = item.code;
                        this.nowPointLabel = item.name
                    }
                    this.options.push({
                        label: item.name,
                        value: item.code,
                    })
                }) : ''
            },
            handPoint(item) {
                store.dispatch('changPoint', {
                    jpcode: this.nowPoint
                }).then(res => {
                    window.location.reload()
                })

            },
            handleCommand(command) {
                if (command === 'logout') { //登出
                    //清除token
                    this.$confirm('是否确认登出？', '', {
                        confirmButtonText: '确定',
                        cancelButtonText: '取消',
                        type: 'warning'
                    }).then(() => {
                        this.LogOut().then(() => {
                            // this.$router.replace({
                            //     path: '/login'
                            // });
                            location.reload();
                        })
                    })
                } else if (command === 'changePassword') { //修改密码
                    this.showModel = true;
                }
            },
            //修改密码
            submit(data) {
                if (!data) {
                    this.showModel = false;
                    return;
                };
                this.loading = true;
                const opt = {
                    ...data
                };
                delete opt.newCopy;
                this.ResetPwd(data).then(res => {
                    this.loading = false;
                    this.$message({
                        type: 'success',
                        message: '修改成功,即将跳转到登录页',
                    })
                    setTimeout(() => {
                        window.location.reload();
                    }, 1500);

                }).catch(res => {
                    this.loading = false;
                })
            }
        }
    }
</script>
<style scoped lang="scss">
    .fixed-header {
        position: fixed;
        height: 0.7rem;
        left: 0;
        top: 0;
        right: 0;
        z-index: 9;
        background: #fff;
        -webkit-transition: width 0.28s;
        transition: width 0.28s;

        .margin-right {
            margin-right: 15px !important;
        }
    }

    .gray-color {
        color: #909CB1;

    }

    .point-text {
        position: absolute;
        top: -500px;
        left: 0;
    }


    .navbar {
        height: 0.7rem;
        padding: 0px 0.1rem 0 0.17rem;
        display: flex;
        justify-content: space-between;
    }

    .logo {
        display: flex;
        padding-left: 0.06rem;
        width: 2.83rem;
        box-sizing: border-box;
    }

    .logo .logo-img {
        width: 1.2rem;
        height: 0.6rem;
        margin-top: 3px;
        vertical-align: top;
        margin-right: 0.1rem;
    }

    .logo span {
        font-size: 0.18rem;
        line-height: 0.74rem;
        display: inline-block;
        font-family: PingFangSC-Medium, PingFang SC;
        font-weight: bold;
        color: #000000;
    }

    .search-box {
        display: flex;
        flex: 1;
        align-items: center;
        font-size: 0.18rem;
        height: 0.7rem;
        box-sizing: border-box;
    }

    .search-box input {
        outline: none;
        border: none;
        height: 0.25rem;
        max-width: 2rem;
        overflow: hidden;
    }

    .right-menu {
        display: flex;
        align-items: center;
        height: 100%;

        /deep/ .el-select {
            width: 100%;
        }

        /deep/ .el-dropdown-link i {
            font-size: 16px;
            transition: all 0.6s;

            &:hover {
                transform: rotate(100deg);
                color: #2E4AF7 !important;
            }
        }
    }

    .right-menu-item {
        display: flex;
        align-items: center;
        font-size: 0.14rem;
        margin: 0 0.15rem;
        height: 100%;
        line-height: 0.56rem;
    }

    .right-menu-item:last-child {
        margin-right: 0;
    }

    .head-img {
        display: flex;
        align-items: center;
        margin-right: 0;
        margin-left: 0.15rem;
        width: 0.34rem;
        height: 0.34rem;
        background: url("../../../assets/head.jpeg");
        background-size: cover;
        background-repeat: no-repeat;
        background-position: 50%;
        border-radius: 50%;
    }

    .el-icon-search {
        font-size: 0.2rem;
        margin-right: 0.13rem;
        vertical-align: middle;
    }

    .el-icon-message-solid {
        font-size: 0.21rem;
    }

    .crew {
        font-size: 0.16rem;
        color: #2e4af7;
    }

    .select-names {
        margin-right: 10px;
        display: flex;
        align-items: center;
        font-size: 0.14rem;
        height: 100%;
        line-height: 0.56rem;

        /deep/ .el-input__inner {
            color: #909CB1;

        }
    }

    .select-names {
        ::v-deep .el-input--suffix .el-input__inner {
            text-align: right;

            border: none;
            outline: none;
        }
    }

    .el-dropdown-link {
        cursor: pointer;
    }

    .logout {
        font-size: 14px !important;

        span {
            margin-left: 10px;
        }
    }
</style>