<template>
    <div>
        <el-menu-item :index="path" v-if="!item.children||item.children.length==1">
            <i v-if="item.icon" class="iconfont" :class="item.icon"></i>
            <span slot="title">{{item.meta.title}}</span>
        </el-menu-item>
        <el-submenu :index="path" v-else>
            <template slot="title">
                <i v-if="item.icon" class="iconfont" :class="item.icon"></i>
                <span>{{item.meta.title}}</span>
            </template>
            <sliderItem v-for="child in item.children" :key="child.path" :item="child" :path="child.path">
            </sliderItem>
        </el-submenu>
    </div>
</template>

<script>
    export default {
        name: 'sliderItem',
        data() {
            return {
                msg: 'Welcome to Your Vue.js App',
                preDom: '',
            }
        },
        props: [
            'path',
            'item',
        ],
        methods: {}
    }
</script>
