<template>
    <div class="slider-aside-out">
        <div class="box">
            <div class="slider-aside">
                <el-menu :default-active="$route.path" class="el-menu-vertical-demo" @select="handleSelect">
                    <slider-item v-for="item in menus" :key="item.name" :item="item" :path="item.path">
                    </slider-item>
                </el-menu>
            </div>
        </div>
    </div>
</template>

<script>
    import SliderItem from './SliderItem'
    import {
        mapGetters
    } from 'vuex'
    import {
        constantRoutes
    } from '@/router'
    import {
        mapState
    } from 'vuex'
    export default {
        components: {
            SliderItem
        },
        name: 'layout',
        data() {
            return {
                // constantRoutes: [],
            }
        },
        computed: {
            ...mapState({
                menus: state => state.permission.menuItems
            }),
        },
        mounted() {
            //修改element menu默认的样式
            const doms = document.querySelectorAll('.el-menu-item');
            const get3P = (_) => _.parentNode.parentNode.parentNode; //获取祖先元素
            doms.forEach(ele => {
                if (get3P(ele).className.indexOf('el-submenu') !== -1) {
                    if (get3P(get3P(ele)).className.indexOf('el-submenu') !== -1) {
                        ele.setAttribute('style', 'padding-left:0.3rem');
                    } else {
                        ele.setAttribute('style', 'padding-left:0.2rem');
                    }
                }
            });
            const doms_ = document.querySelectorAll('.el-submenu__title');
            doms_.forEach(ele => {

                ele.setAttribute('style', 'padding-left:0.2rem');
                if (get3P(ele).parentNode.className.indexOf('slider-aside') !== -1) {
                    return;
                }
                ele.setAttribute('style', 'font-size:0.16rem');
            });
        },
        methods: {
            handleSelect(key, keyPath) { // 路由跳转
                this.$router.push({
                    path: keyPath[keyPath.length - 1]
                });
            },
        }
    }
</script>
<style scoped lang="scss">
    .slider-aside-out {
        position: fixed;
        left: 0;
        top: 0.7rem;
        bottom: 0;
        width: 2.7rem;
        height: calc(100% - 0.7rem);
        font-size: 0;
        overflow: hidden;

        .box {
            width: 100%;
            height: 100%;
            position: relative;
            overflow: hidden;

            .slider-aside {
                position: absolute;
                left: 0;
                top: 0;
                bottom: 0;
                right: -10px;
                -webkit-transition: width 0.28s;
                transition: width 0.28s;
                font-size: 0;
                overflow-y: auto;
                padding-bottom: 20px;

            }
        }
    }



    .unit-box {
        padding: 0 0.28rem;
    }

    .unit-box>div {
        width: 2.16rem;
        height: 0.96rem;
        background: rgba(46, 74, 247, 0.12);
        border-radius: 0.06rem;
        padding: 0.17rem 0.32rem 0 0.17rem;
        box-sizing: border-box;
    }

    .unit-box>div div {
        height: 0.31rem;
        line-height: 0.31rem;
        text-align: left;
        color: #333333;
        font-size: 0.16rem;
    }
</style>