<template>
    <!--修改密码-->
    <el-dialog append-to-body title="修改密码" custom-class="cusDialog" :visible.sync="show" width="4.4rem"
        :before-close="handleClose">
        <el-form class="form" :model="selectedObj" :rules="rules" ref="form">
            <div class="li">
                <div class="item">
                    <el-form-item prop="oldPassword">
                        <div class="label"><span class="important">*</span>旧密码：</div>
                        <el-input type="password" v-model="selectedObj.oldPassword" placeholder="请输入当前登录密码">
                        </el-input>
                    </el-form-item>
                </div>
            </div>
            <div class="li">
                <div class="item">
                    <el-form-item prop="newPassword">
                        <div class="label"><span class="important">*</span>新密码：</div>
                        <el-input type="password" v-model="selectedObj.newPassword" placeholder="请输入新密码">
                        </el-input>
                    </el-form-item>
                </div>
            </div>
            <div class="li">
                <div class="item">
                    <el-form-item prop="newCopy">
                        <div class="label"><span class="important">*</span>新密码：</div>
                        <el-input type="password" v-model="selectedObj.newCopy" placeholder="请再次输入新密码">
                        </el-input>
                    </el-form-item>
                </div>
            </div>

        </el-form>
        <span slot="footer" class="dialog-footer">
            <el-button type="primary" :loading="loading" @click="submit">提 交</el-button>
            <el-button @click="handleClose">关 闭</el-button>
        </span>
    </el-dialog>
</template>
<script>
    import {
        Message
    } from 'element-ui';

    import {
        parseTime
    } from "@/utils/index";
    export default {
        props: {
            show: {
                type: Boolean,
                default: false,
            },
            loading: Boolean
        },
        data() {
            return {
                rules: {
                    oldPassword: [{
                        required: true,
                        message: '请输入旧密码',
                        trigger: 'blur'
                    }],

                    newPassword: [{
                        required: true,
                        message: '请输入新密码',
                        trigger: 'blur'
                    }],
                    newCopy: [{
                        validator: (rule, value, callback) => {
                            if (value === '') {
                                callback(new Error('请再次输入新密码'));
                            } else if (value !== this.selectedObj.newPassword) {
                                callback(new Error('两次输入密码不一致!'));
                            } else {
                                callback();
                            }
                        },
                        trigger: 'blur'
                    }]
                },

                selectedObj: {},
            }
        },

        methods: {
            parseTime,
            handleClose() {
                this.$emit('submit')
            },
            submit() {
                this.$refs['form'].validate((valid) => {
                    if (valid) {
                        this.selectedObj.confirmPassword = this.selectedObj.newPassword;
                        this.$emit('submit', this.selectedObj)
                    }
                })
            }
        },
    }
</script>

<style scoped lang="scss">
    .form {
        .li {

            /deep/ .el-form-item {
                margin-bottom: 0;
                width: 100%;
            }

            /deep/ .el-input__inner {
                width: 2.2rem;
            }

            display: flex;
            margin-bottom: 22px;

            .item {
                width: 100%;
                display: flex;
                line-height: 32px;
                height: 32;

                .label {
                    font-family: "PFC";
                    min-width: 1rem;
                    margin-right: 2px;
                    color: #333;
                    text-align: right;
                }

                .box {
                    width: 100%;
                    border-radius: 4px;
                    color: #666;
                    box-sizing: border-box;
                }

                .rt {
                    flex: 1;
                }

                .btns {
                    width: 100%;
                    display: flex;
                    justify-content: flex-end;

                }
            }
        }
    }
</style>